

.expirience-container{
    -webkit-box-shadow: 0px 3px 35px -6px rgba(34, 60, 80, 0.2);
-moz-box-shadow: 0px 3px 35px -6px rgba(34, 60, 80, 0.2);
box-shadow: 0px 3px 35px -6px rgba(34, 60, 80, 0.2);

}

 .expirience-container__item img{
     width: 100%;
 }
 .expirience-props-element{
     margin-top: 10px;
 }
 .expirience-container__item-content{
     padding-top: 5px;
     padding-left: 5px;
     padding-bottom: 5px;
     font-family: 'Montserrat';

 }
 .container__item-nameContainer-name span{
    font-weight: 700;
 }
 .container__item-nameContainer-stack span{
    font-weight: 700;
 }
 .container__item-nameContainer-gitHub span{
    font-weight: 700;
 }
 .container__item-nameContainer-webName span{
    font-weight: 700;

 }

